import { controllerJakPsatOUprchlicich } from '../controllers/jak-psat-o-uprchlicich/controller';
import { controllerPromenyMestaZlin } from '../controllers/promeny-mesta-zlin/controller';
import { controllerPromenyObceAbertamy } from '../controllers/promeny-obce-abertamy/controller';
import { controllerWhatHappenedAtCroydonAirport } from '../controllers/what-happened-at-croydon-airport/controller';

const controllerLoad = () => {

  const pageControllerName = document.querySelector('main.cviceni').id;

  // - inject:js

  if (pageControllerName === 'jak-psat-o-uprchlicich') {
    controllerJakPsatOUprchlicich();
  }

  if (pageControllerName === 'promeny-mesta-zlin') {
    controllerPromenyMestaZlin();
  }


  if (pageControllerName === 'promeny-obce-abertamy') {
    controllerPromenyObceAbertamy();
  }

  if (pageControllerName === 'what-happened-at-croydon-airport') {
    controllerWhatHappenedAtCroydonAirport();
  }

  // - end inject:js

};

export default controllerLoad;
