/* eslint-disable import/prefer-default-export */
export const controllerPromenyMestaZlin = () => {

  const $buttonNexts = document.querySelectorAll('[data-nav-button="next"');
  const $razeniContainer = document.getElementById('patrani-razeni-1');
  // .getElementsByClassName() returns a live collection which is needed here
  const $razeniContainerItems = $razeniContainer.getElementsByClassName('sortable-item');

  // use [data-slide-index="8"] isntead of ID becasue slug can change… let see if it is more reliable
  const $svgContainer = document.querySelector('[data-slide-index="8"] .sources');
  const $svgContainerItems = $svgContainer.querySelectorAll('.source');

  function changeOrderOnNavButtonClick($buttonNext) {
    $buttonNext.addEventListener('click', () => {

      Array.from($razeniContainerItems).forEach(($referenceItem) => {

        const thisImageSrcParts = $referenceItem.querySelector('.source-image').getAttribute('src').split('/');
        const thisImageName = thisImageSrcParts[thisImageSrcParts.length - 1];

        // loop over svg images and find the one with the same src
        Array.from($svgContainerItems).some(($svgItem) => {

          const getSvgThisImageParts = $svgItem.querySelector('image').getAttribute('xlink:href').split('/');
          const thisSvgImageName = getSvgThisImageParts[getSvgThisImageParts.length - 1];


          if (thisSvgImageName === thisImageName) {
            $svgContainer.appendChild($svgItem);

            return true;
          }

          return false;
        });

      });

    });
  }

  $buttonNexts.forEach(($buttonNext) => changeOrderOnNavButtonClick($buttonNext));

};
