/* eslint-disable import/prefer-default-export */
import { __addClass, __hasClass, __triggerEvent } from '../../lib/utils';


export const controllerJakPsatOUprchlicich = () => {

  const $popis1 = document.querySelector('#popis-interpretace-1');
  const $popis2 = document.querySelector('#popis-interpretace-2');
  const $popis3 = document.querySelector('#popis-interpretace-3');
  const $popis4 = document.querySelector('#popis-interpretace-4');
  const $popis5 = document.querySelector('#popis-interpretace-5');
  const $popis6 = document.querySelector('#popis-interpretace-6');
  const $popis7 = document.querySelector('#popis-interpretace-7');
  const $popis8 = document.querySelector('#popis-interpretace-8');

  const changedClass = 'changed';


  $popis1.addEventListener('change', (e) => {

    const $this = e.currentTarget;
    const $next = $popis2;

    // changed next textbox only if it hasn't been changed
    if (!__hasClass($next, changedClass)) {

      $next.value = $this.value;
      __triggerEvent($next, 'change');

    }


  });

  $popis2.addEventListener('change', (e) => {

    const $this = e.currentTarget;
    const $next = $popis4;

    // changed next textbox only if it hasn't been changed
    if (!__hasClass($next, changedClass)) {

      $next.value = $this.value;

    }

    __addClass($this, changedClass);

  });


  $popis3.addEventListener('change', (e) => {

    const $this = e.currentTarget;
    const $next = $popis5;

    // changed next textbox only if it hasn't been changed
    if (!__hasClass($next, changedClass)) {

      $next.value = $this.value;

    }

    __addClass($this, changedClass);
    __triggerEvent($next, 'change');

  });


  $popis4.addEventListener('change', (e) => {

    const $this = e.currentTarget;


    __addClass($this, changedClass);

  });


  $popis5.addEventListener('change', (e) => {

    const $this = e.currentTarget;
    const $next = $popis6;

    // changed next textbox only if it hasn't been changed
    if (!__hasClass($next, changedClass)) {

      $next.value = $this.value;

    }

    __addClass($this, changedClass);

  });

  $popis6.addEventListener('change', (e) => {

    const $this = e.currentTarget;

    __addClass($this, changedClass);

  });


  $popis7.addEventListener('change', (e) => {

    const $this = e.currentTarget;
    const $next = $popis8;

    // changed next textbox only if it hasn't been changed
    if (!__hasClass($next, changedClass)) {

      $next.value = $this.value;

    }

    __addClass($this, changedClass);

  });

  $popis8.addEventListener('change', (e) => {

    const $this = e.currentTarget;

    __addClass($this, changedClass);

  });

};
